<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isBankAll')">{{ $t('MODIFY') }}</vs-button>
    </div>

    <div class="row hover mb-base">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ teller.tellerId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('TELLER_NAME') }}:</span>
        <span>
          <span :class="disabledClass(teller)">{{ teller.tellerName }}</span>
        </span>
      </p>
      <p class="info-row">
        <span>{{ $t('TELLER_TYPE') }}:</span>
        <span>{{ teller.tellerTypeName }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('TELLER_STATUS') }}:</span>
        <span>{{ teller.tellerStatusName }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('PSEUDO_ID') }}:</span>
        <span>{{ teller.pseudoId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('EXTERNAL_ID') }}:</span>
        <span>{{ teller.externalId | placeholder('-') }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('CREATION_DATE') }}:</span>
        <span>{{ teller.creationDate | formatUnixDate }}</span>
      </p>
    </div>

    <vs-collapse type="border" v-if="bankFunctionList.length > 0 || hasCoordinates">
      <vs-collapse-item v-if="bankFunctionList.length > 0" :open="true">
        <div slot="header">
          {{ $t('FUNCTIONS') }}
        </div>
        <div class="vs-row flex justify-start">
          <div v-for="(bankFunction, itemIndex) in bankFunctionList" class="nv-col sm:w-1/6 w-full p-2" :key="itemIndex">
            <i class="text-primary mr-1" :class="bankFunction.iconClass"></i> <span :class="disabledClass(bankFunction)">{{ bankFunction.functionName }}</span>
          </div>
        </div>
      </vs-collapse-item>
      <vs-collapse-item v-if="hasCoordinates" :open="true">
        <div slot="header">
          {{ $t('ADDRESS') }}
        </div>
        <GmapMap
          v-if="hasCoordinates"
          :center="{lat:parseFloat(teller.latitude), lng:parseFloat(teller.longitude)}"
          :zoom="13"
          map-type-id="terrain"
          style="width: 100%; height: 400px">
          <GmapMarker
            :position="{lat:parseFloat(teller.latitude), lng:parseFloat(teller.longitude)}"
            :clickable="false"
            :draggable="false"/>
        </GmapMap>
      </vs-collapse-item>
    </vs-collapse>
  </nv-card>
</template>

<script>
import TellerService from '@/services/bank/TellerService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'

export default {
  name: 'teller-view',
  mixins: [DisabledClassMixin],
  data: function () {
    return {
      teller: {},
      popupActive: false,
      bankFunctionList: []
    }
  },
  methods: {
    async load () {
      let self = this
      TellerService.tellers({
        tellerId: this.$route.params.id
      }).then(function (response) {
        self.teller = response.data.tellerList[0] || {}
      })

      TellerService.tellerFunctions({
        tellerId: this.$route.params.id
      }).then(function (response) {
        self.bankFunctionList = response.data.tellerBankFunctionList || []
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyTeller', params: { id: this.$route.params.id } })
    }
  },
  computed: {
    hasCoordinates () {
      return this.teller.latitude && this.teller.longitude
    }
  },
  mounted () {
    this.load()
  }
}
</script>
