import Endpoint from '@/services/Endpoint'

export default {
  tellers (payload) {
    return Endpoint().get('/s1/bank/teller', {
      params: payload
    })
  },
  createTeller (payload) {
    return Endpoint().post('/s1/bank/teller', payload)
  },
  modifyTeller (payload) {
    return Endpoint().patch('/s1/bank/teller', payload)
  },
  tellerFunctions (payload) {
    return Endpoint().get(`/s1/bank/teller/${payload.tellerId}/bankFunction`, {
      params: payload
    })
  }
}
